<template>
  <div>

    <ComFrontnavbar></ComFrontnavbar>

    <!-- jumbotron大廣告 -->
    <!-- <div class="jumbotron jumbotron-fluid jumbotron-bg4 mb-0">
      <div class="container jumbotronStyle001">
        <img class="jbimg__boxer" :src="baseeight[0].img" alt="">
      </div>
    </div> -->

    <!-- 產品介紹 -->
    <section style="background-color: #ffffff;">

      <div class="container" style="background-color: #ffffff;">

        <div class="row">
          <div class="col-md-12">
            <div>
              <div class="productStyle002">
                <i class="fas fa-star text-light"></i><i class="far fa-star text-light"></i><h2 class="emailStyle001">我的訂單</h2><i class="far fa-star text-light"></i><i class="fas fa-star text-light"></i>      
              </div>
              <div>
                <hr class="hrStyle2 mt-0">
              </div>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-12 col-sm">
            <div class="alert alert-primary alert-rounded" style="text-align: center; font-size: 16px;" role="alert">
              輸入資料訂單
            </div>
          </div>

          <div class="col-12 col-sm">
            <div class="alert alert-dark alert-rounded" style="text-align: center; font-size: 16px;" role="alert">
              金流付款
            </div>
          </div>
    
        </div>

        <div class="row" style="font-size: 16px; font-weight: bold">
          <div class="col-md-3 mb-5 mt-4"></div>
          <div class="col-md-6 mb-5 mt-4">


              <div class="m-5 row justify-content-center">
                <table class="table">
                  <thead>
                    <th></th>
                    <th>品名</th>
                    <th>數量</th>
                    <th style="text-align: right">單價</th>
                  </thead>
                  <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" v-if="cart.carts">
                      <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                          <i class="far fa-trash-alt"></i>
                        </button>
                      </td>
                      <td class="align-middle">
                        {{ item.product.title }}
                        <div class="text-success" v-if="item.coupon">
                          已套用優惠券
                        </div>
                      </td>
                      <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                      <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colspan="3" class="text-right">總計</td>
                      <td class="text-right">{{ cart.total }}</td>
                    </tr>
                    <tr v-if="cart.final_total !==cart.total" >
                      <td colspan="3" class="text-right text-success">折扣價</td>
                      <td class="text-right text-success">{{ cart.final_total }}</td>
                    </tr>
                  </tfoot>
                </table>

                <div class="input-group mb-3 input-group-sm">
                  <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                  <div class="input-group-append">
                    <button class="btn btn-primary font-weight-bold" type="button" @click="addCouponCode">
                      套用優惠碼
                    </button>
                  </div>
                </div>
              </div>
     
              <div class="my-5 row justify-content-center">
                <form class="col-md-8" @submit.prevent="createOrder">
                  <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                      v-validate="'required|email'"
                      :class="{'is-invalid': errors.has('email')}"
                      v-model="form.user.email" placeholder="請輸入 Email">
                    <span class="text-danger" v-if="errors.has('email')">
                      {{ errors.first('email') }}
                    </span>
                  </div>

                  <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" name="name" id="username"
                      :class="{'is-invalid': errors.has('name')}"
                      v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
                    <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
                  </div>

                  <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input type="tel" class="form-control" name="tel" id="usertel"
                      v-model="form.user.tel" v-validate="'required'" placeholder="請輸入電話">  
                    <span class="text-danger" v-if="errors.has('tel')">電話不能留空</span>     
                  </div>

                  <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="address" class="form-control" name="address"
                          :class="{'is-invalid': errors.has('address')}"
                      id="useraddress" v-model="form.user.address" v-validate="'required'"
                      placeholder="請輸入地址">
                    <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                  </div>

                  <div class="form-group">
                    <label for="usermessage">留言</label>
                    <textarea name="messagetextarea" id="messagearea" class="form-control" cols="30" rows="10"
                      v-model="form.message"  v-validate="'required'"  placeholder="請輸入留言"></textarea>
                    <span class="text-danger" v-if="errors.has('messagetextarea')">留言內容不能留空</span> 
                  </div>
                  
                  <div class="text-right">
                    <button class="btn btn-danger font-weight-bold">送出訂單</button>
                  </div>
                </form>
              </div>   



          </div>             
        </div>
      </div>

    </section>



    <ComFrontfooter></Comfrontfooter>

  </div>  

</template>

<script>
import ComFrontnavbar from './frontpages/ComFrontnavbar';
import ComFrontfooter from './frontpages/ComFrontfooter';
import $ from 'jquery';

export default {
  data() {
    return {

      baseeight :[
        {
          id: 0,
          img: '/static/images/boxer.png',
        }, 
      ],
      coupon_code: '',
      products: [],
      product: {},
      status: {
        loadingItem: '',
      },

      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },      

      cart:{},
      isLoading: false,
      coupon_code: '',
    };
  },

  components: {
    ComFrontnavbar,
    ComFrontfooter,
  },

  methods: {

    getProducts() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.products = response.data.products;
        console.log(response);
        vm.isLoading = false;
      });
    },

    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $('#productModal').modal('show');
        console.log(response);
        vm.status.loadingItem = '';
      });
    },

    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty,
      };
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response);
        vm.status.loadingItem = '';
        vm.getCart();
        $('#productModal').modal('hide');
      });
    },

    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        // vm.product = response.data.products;
        vm.cart = response.data.data;
        console.log(response);
        vm.isLoading = false;
      }); 
    },

    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },

    addCouponCode() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
        code: vm.coupon_code
      }
      vm.isLoading = true;
      this.$http.post(url, {data: coupon} ).then((response) => {
        console.log(response);
        vm.getCart();
        vm.isLoading = false;
      });
    },  

    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(url, { data: order }).then((response) => {
            console.log('訂單已建立', response);
            if(response.data.success){
              vm.$router.push(`/frontmycheckout/${response.data.orderId}`);
            }
            // vm.getCart();
            vm.isLoading = false;
          });
        } else {
          console.log('欄位不完整');
        }
      });
    },

  },

  created() {

    this.getProducts();
    this.getCart();
    
  },

};
</script>





  


